import LinkTo from '../../atoms/LinkTo/LinkTo';

interface Props {
  text: string;
  href: string;
  className?: string;
}

export default function Button(props: Props) {
  const { href, text, className } = props;
  return (
    <LinkTo
      href={href}
      className={`${
        className || ''
      } select-none rounded-md bg-primary/90 p-2 text-2xl font-bold shadow hover:shadow-xl md:text-white lg:block lg:bg-transparent lg:text-primary`}
    >
      {text}
    </LinkTo>
  );
}
