import { ReactNode } from 'react';

interface Props {
  children: ReactNode;
}

export default function ButtonBannerContainer(props: Props) {
  const { children } = props;
  return (
    <div className="absolute top-0 left-0 z-20 h-screen w-full font-primary md:px-20 lg:px-10">
      <div className="flex h-screen items-center justify-between">
        {children}
      </div>
    </div>
  );
}
