import { useState } from 'react';
import BannerPopup from '../../atoms/BannerPopup/BannerPopup';
import Button from './Button';

export interface ButtonGroupSideProps {
  link: {
    one: string;
    two: string;
  };
  label: {
    one: string;
    two: string;
  };
}

export default function ButtonGroupSide(props: ButtonGroupSideProps) {
  const { label, link } = props;
  const [showBannerPopup, setShowBannerPopup] = useState(false);
  if (link.one === '/ivf-academy') {
    return (
      <>
        {showBannerPopup && <BannerPopup onClose={setShowBannerPopup} />}
        <div className="flex h-screen flex-col items-center justify-between">
          <div className="flex-1" />
          <div className="flex w-[21vw] flex-1 flex-col items-center justify-between ">
            <div className="mb-8">
              <button
                type="button"
                className="block h-[16vh] w-[21vw]"
                onClick={() => setShowBannerPopup(true)}
              >
                {' '}
              </button>
            </div>
            <div className="h-5" />
            <div className="mb-6 flex flex-col gap-2">
              <Button href={link.one} text={label.one} />
              <Button href={link.two} text={label.two} />
            </div>
            <div className="h-8" />
          </div>
          <div className="flex-1" />
        </div>
      </>
    );
  }
  return (
    <div className="flex h-screen flex-col items-center justify-between">
      <div className="flex-1" />
      <div className="flex w-[21vw] flex-1 flex-col items-center justify-between ">
        <div className="mb-8 bg-white/0 hover:bg-white/0">
          <Button
            href={link.one}
            text={label.one}
            className="w-[19vw] py-4 text-center"
          />
        </div>
        <div className="h-5" />
        <div className="mb-6 bg-white/0 hover:bg-white/0 ">
          <Button
            href={link.two}
            text={label.two}
            className="w-[19vw] py-4 text-center"
          />
        </div>
      </div>
      <div className="h-8" />
      <div className="flex-1" />
    </div>
  );
}
