import { motion } from 'framer-motion';
import { Carousel } from 'react-responsive-carousel';
import { animationsFade } from '../../pages/Booth/CompanyProfile';
import OverlayPopup from '../../pages/Booth/OverlayPopup';

interface Props {
  onClose: (e: boolean) => void;
}

export default function BannerPopup(props: Props) {
  const { onClose } = props;
  return (
    <motion.div
      variants={animationsFade}
      initial="initial"
      animate="animate"
      exit="exit"
      transition={{ duration: 0.2 }}
    >
      <OverlayPopup onClose={onClose}>
        <Carousel
          showArrows
          showThumbs
          autoPlay
          interval={5000}
          infiniteLoop
          stopOnHover
          swipeable
          emulateTouch
          thumbWidth={100}
          showStatus
          className="mb-5 p-5 shadow lg:min-h-[500px]"
        >
          <div>
            <img
              src="/img/banners/banner-1.jpg"
              alt="banner-1"
              className="rounded-lg"
            />
          </div>
          <div>
            <img
              src="/img/banners/banner-2.jpg"
              alt="banner-2"
              className="rounded-lg"
            />
          </div>
          <div>
            <img
              src="/img/banners/banner-3.jpg"
              alt="banner-3"
              className="rounded-lg"
            />
          </div>
        </Carousel>
      </OverlayPopup>
    </motion.div>
  );
}
