import ButtonBannerContainer from '../../containers/ButtonBannerContainer/ButtonBannerContainer';
import ButtonGroupSide, { ButtonGroupSideProps } from './ButtonGroupSide';

const labelAndLinkLeft: ButtonGroupSideProps = {
  label: {
    one: 'IVF Academy',
    two: 'Webinar Series',
  },
  link: {
    one: '/ivf-academy',
    two: '/webinar-series',
  },
};

const labelAndLinkRight: ButtonGroupSideProps = {
  label: {
    one: 'Vacancy',
    two: 'Expo Hall',
  },
  link: {
    one: '/vacancy',
    two: '/expo-hall',
  },
};

export default function ButtonBanner() {
  return (
    <ButtonBannerContainer>
      <ButtonGroupSide {...labelAndLinkLeft} />
      <ButtonGroupSide {...labelAndLinkRight} />
    </ButtonBannerContainer>
  );
}
