import { useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';
import { useEffect, useState } from 'react';
import { Helmet } from 'react-helmet';
import { isMobile } from 'react-device-detect';
import { titlePageGenerator } from '../../../utils';
import TransitionLayer from '../../atoms/TransitionLayer/TransitionLayer';
import ButtonBanner from '../../molecules/ButtonBanner/ButtonBanner';
import OpenInComputer from '../OpenInComputer/OpenInComputer';
import TawkToWrapper from '../../atoms/TawkToWrapper/TawkToWrapper';

function LandingPage() {
  const [isVideoReady, setIsVideoReady] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    if (isMobile) {
      navigate('/vacancy');
      toast.warn('Halaman ini tidak tersedia di perangkat mobile');
    }
  }, []);

  if (isMobile) {
    return <OpenInComputer />;
  }

  return (
    <div className="h-screen w-full overflow-hidden">
      <Helmet>
        <title>{titlePageGenerator('Welcome')}</title>
      </Helmet>
      <div className="relative">
        <video
          onLoadedData={() => setIsVideoReady(true)}
          className="z-10 h-screen w-full object-cover"
          autoPlay
          muted
          loop
          src="/videos/visual-gate.mp4"
        >
          <track kind="video gate" />
        </video>
        <ButtonBanner />
        <TransitionLayer showTransition={isVideoReady} />
      </div>
    </div>
  );
}

export default LandingPage;
